"use client"

import { useState, useMemo } from "react"
import { BottomNavigation } from "@/components/guest/bottom-navigation"
import { CartDrawer } from "@/components/guest/cart-drawer"
import { CategoryTabs } from "@/components/guest/category-tabs"
import { ItemDetailModal } from "@/components/guest/item-detail-modal"
import { MenuHeader } from "@/components/guest/menu-header"
import { MenuItemCard } from "@/components/guest/menu-item-card"
import { useCart } from "@/lib/cart-context"
import { categories, menuItems, type MenuItem, type ModifierOption } from "@/lib/menu-data"

export default function GuestMenuPage() {
  const [searchQuery, setSearchQuery] = useState("")
  const [activeCategory, setActiveCategory] = useState("all")
  const [isCartOpen, setIsCartOpen] = useState(false)
  const [selectedItem, setSelectedItem] = useState<MenuItem | null>(null)
  const [isDetailOpen, setIsDetailOpen] = useState(false)

  const { dispatch } = useCart()

  const filteredItems = useMemo(() => {
    return menuItems.filter((item) => {
      const matchesSearch =
        item.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        item.description.toLowerCase().includes(searchQuery.toLowerCase())
      const matchesCategory = activeCategory === "all" || item.category === activeCategory
      return matchesSearch && matchesCategory
    })
  }, [searchQuery, activeCategory])

  const handleQuickAdd = (item: MenuItem) => {
    if (item.modifiers && item.modifiers.length > 0) {
      setSelectedItem(item)
      setIsDetailOpen(true)
    } else {
      dispatch({
        type: "ADD_ITEM",
        payload: {
          menuItem: item,
          quantity: 1,
          selectedModifiers: [],
          totalPrice: item.price,
        },
      })
    }
  }

  const handleAddToCart = (item: MenuItem, quantity: number, modifiers: ModifierOption[], notes: string) => {
    const modifierTotal = modifiers.reduce((sum, mod) => sum + mod.price, 0)
    dispatch({
      type: "ADD_ITEM",
      payload: {
        menuItem: item,
        quantity,
        selectedModifiers: modifiers,
        notes,
        totalPrice: item.price + modifierTotal,
      },
    })
  }

  return (
    <div className="min-h-screen bg-background pb-20">
      <MenuHeader searchQuery={searchQuery} onSearchChange={setSearchQuery} tableId="12" />

      <CategoryTabs categories={categories} activeCategory={activeCategory} onCategoryChange={setActiveCategory} />

      <main className="p-4">
        {filteredItems.length === 0 ? (
          <div className="flex flex-col items-center justify-center py-12 text-center">
            <p className="text-muted-foreground">Kh√¥ng t√¨m th·∫•y m√≥n ƒÉn ph√π h·ª£p</p>
          </div>
        ) : (
          <div className="grid grid-cols-2 gap-3 sm:grid-cols-3 md:grid-cols-4">
            {filteredItems.map((item) => (
              <MenuItemCard
                key={item.id}
                item={item}
                onAddToCart={() => handleQuickAdd(item)}
                onClick={() => {
                  setSelectedItem(item)
                  setIsDetailOpen(true)
                }}
              />
            ))}
          </div>
        )}
      </main>

      <BottomNavigation onCartClick={() => setIsCartOpen(true)} />

      <CartDrawer isOpen={isCartOpen} onClose={() => setIsCartOpen(false)} />

      <ItemDetailModal
        item={selectedItem}
        isOpen={isDetailOpen}
        onClose={() => {
          setIsDetailOpen(false)
          setSelectedItem(null)
        }}
        onAddToCart={handleAddToCart}
      />
    </div>
  )
}
